.App{
    width: 100vw;
    height: fit-content;
    display: flex;
    align-items: center;
    
}
.background{
    width: 493px;
    height: 900px;
    background-image: url(./validimg/bg-main-desktop.png);
    position: relative;
}

.front{
    position: absolute;
    top: 187px;
    left: 184px;
    width: 447px;
    height: 245px;
    border-radius: 10px;
    background-image: url(./validimg/bg-card-front.png);
}
.back{
    position: absolute;
    top: 469px;
    left: 258px;
    width: 447px;
    height: 245px;
    border-radius: 10px;
    background-image: url(./validimg/bg-card-back.png);
}
.saveCvc{
    margin-top: 111px;
    margin-left: 358px;
    font-size: 14px;
    color: #FFFFFF;
    font-family: "regularSpace";
}
.validation{
    width: 381px;
    margin-left: 349px;
    font-family: "regularSpace";
    font-size: 12px;
}
.enterName{
    width: 100%;
}
.nameInput{
    border: 1px solid #DFDEE0;
    border-radius: 8px;
    background: #FFFFFF;
    width: 100%;
    height: 45px;
    margin-top: 9px;
    font-size: 18px;
    padding-left: 16px;
}

.blank{
    color: #FF5050;
    margin-top: 8px;
}
.enterNumbers{
    width: 100%;
    margin-top: 26px;
}

.numberInput{
    border: 1px solid #DFDEE0;
    border-radius: 8px;
    background: #FFFFFF;
    width: 100%;
    height: 45px;
    margin-top: 9px;
    font-size: 18px;
    padding-left: 16px;
}
.dataSection{
    width: 100%;
    margin-top: 26px;
    display: flex;
}
.month{
    width: 80px;
    height: 45px;
    border: 1px solid #DFDEE0;
    border-radius: 8px;
    padding-left: 16px;
    
}
/* .month::-webkit-inner-spin-button{
    -webkit-appearance: none;
} */
.year{
    width: 80px;
    height: 45px;
    border: 1px solid #DFDEE0;
    border-radius: 8px;
    padding-left: 16px;
    margin-left: 10px;
}
.cvcdiv{
    margin-left: 20px;
}

.cvc{
    width: 191px;
    height: 45px;
    border: 1px solid #DFDEE0;
    border-radius: 8px;
    padding-left: 16px;
    margin-top: 8px;
}

::placeholder{
    color: #21092F;
    opacity: 0.25;
}

.confirm{
    margin-top: 40px;
    width: 100%;
    height: 45px;
    border-radius: 8px;
    background: #21092F;
    color: #FFFFFF;
    font-size: 18px;
}
#nameId{
    border: 1px solid red;
}
#numberId{
    border: 1px solid red;
}
#monthId{
    border: 1px solid red;
}
#yearId{
    border: 1px solid red;
}
#cvcId{
    border: 1px solid red;
}

.saved{
    width: 381px;
    margin-left: 349px;
    font-family: "regularSpace";
    font-size: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.thanks{
    font-size: 28px;
    color: #21092F;
    margin-top: 35px;
}
.added{
    color: #8F8694;
    font-size: 18px;
    margin-top: 16px;
}
.continue{
    margin-top: 48px;
    width: 100%;
    height: 45px;
    border-radius: 8px;
    background: #21092F;
    color: #FFFFFF;
    font-size: 18px;
}